@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: "Satisfy", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.recipeList {
  cursor: pointer;
}

.recipeList__img {
  height: 200px;
  width: 200px;
  object-fit: cover;
  border-radius: 0.25rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.recipeList__img:hover {
  height: 200px;
  width: 200px;
  object-fit: cover;
  border-radius: 0.25rem;
  opacity: 0.5;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.recipeList__name {
  max-width: 200px;
  margin: 1rem 0;
  margin-left: auto;
  margin-right: auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

  font-weight: bold;
}

.recipeList__ingredientLines {
  margin: 1rem 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

  -webkit-align-items: baseline;

          align-items: baseline;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 3rem 2rem;
}

.app__heading {
  font-size: 1rem;
  margin: 1rem;
}
@media (min-width: 320px) {
  .app__heading {
    font-size: 1.6rem;
  }
  .app_searchForm .app__healthLabels {
    min-width: 32vw;
    max-width: 6vw;
  }
}
@media (min-width: 374px) {
  .app__heading {
    font-size: 2rem;
  }
}
@media (min-width: 424px) {
  .app__heading {
    font-size: 2.4rem;
  }

  .app_searchForm .app__input {
    min-width: 25vw;
    max-width: 6vw;
  }
  .app_searchForm .app__healthLabels {
    min-width: 25vw;
    max-width: 6vw;
  }
}
@media (min-width: 574px) {
  .app__heading {
    font-size: 3.5rem;
  }
}
@media (min-width: 768px) {
  .app__heading {
    font-size: 4rem;
  }
  .app_searchForm .app__input {
    min-width: 30vw;
    max-width: 6vw;
  }
}
@media (min-width: 1023px) {
  .app__heading {
    font-size: 6rem;
  }
}

.app_searchForm {
  margin-top: 1rem;
}

.app__input {
  padding: 0.5rem 1rem;
  min-width: 200px;
  max-width: 100vw;
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.app__submit {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  margin-left: 0.5rem;
  background-color: #ed5a6b;
  border: 2px solid #ed5a6b;
  border-radius: 0.25rem;
  color: #ffffff;
  cursor: pointer;
}

.app__submit:hover {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  margin-left: 0.5rem;
  background-color: #ffffff;
  border: 2px solid #ed5a6b;
  border-radius: 0.25rem;
  color: #ed5a6b;
  cursor: pointer;
}

.app__healthLabels {
  padding: 0.5rem 1rem;
  min-width: 100px;
  max-width: 100vw;
  font-size: 1rem;
  margin-left: 0.5rem;
  border-radius: 4px;
}

.app_recipe {
  margin-top: 2rem;
  padding: auto;
  display: grid;
  -webkit-align-items: baseline;
          align-items: baseline;
}

/* Screen larger than 600px? 2 column */
@media (min-width: 600px) {
  .app_recipe {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Screen larger than 900px? 3 columns */
@media (min-width: 900px) {
  .app_recipe {
    grid-template-columns: repeat(3, 1fr);
  }
}

